.sticky {
	display: block;
}

.hentry {
	margin: 0 0 1.5em;
}

.entry-header {
    
    font-family:$font__sans;
        
}

.cat-links {
    font-size: 90%;
    font-weight:700;
    
    a{
        text-decoration: none;
        text-transform: uppercase;
        color: $color__interactive; 
        
        &:focus,
        &:hover{
            background-color: $color__interactive;
            color:white;
        }
    }
    
}

.entry-title {
    margin: .125em 0 .25em;
    font-size: 2.5em;
    
    @media screen and (min-width: $query__medium){
        font-size: 3em;   
    }
    
    a{
        text-decoration: none;
        color:black;
        
        &:focus,
        &:hover{
            border-bottom: 5px solid $color__interactive;
        }
        
    }
}

.entry-meta
.entry-footer{
    font-family: $font__sans;
    font-size: 90%;
    
    a{
        font-weight: 700;
        text-decoration: none;
        color:black;
        &:focus,
        &:hover{
             border-bottom: 5px solid $color__interactive;
        }
    }
}

.byline{
    &::after{
        content: "|";
        margin: 0, 5em;
    }
}

.comments-link,
.edit-link{
    &::before{
        content: "|";
        margin: 0, 5em;
    }
}




.updated:not(.published){
	display: none;
}

.page-content,
.entry-content,
.entry-summary {
	margin: 1.5em 0 0;
}

.page-links {
	clear: both;
	margin: 0 0 1.5em;
}


.extra{
    display: none;
}



.no-sidebar{
    
    .entry-meta{
        margin-top: 1.5em;
    }
    
    @media screen and (min-width: $query__medium){
        .post-content__wrap{
            
            .entry-meta{
                margin-top: 2.25em;
                font-size: 80%;
                
                
            .byline{
                &::after{
                    content: "";
                    margin: 0, 5em;
                }
            }

            .comments-link,
            .edit-link{
                &::before{
                    content: "";
                    margin: 0, 5em;
                }
            }
                
                
                
                
                
                
                
                
                a{
                    
                    font-size: 110%;
                    display:block;
                    border-bottom: 3px solid white;
                    
                    &:hover,
                        &:focus{
                        border-bottom-color: $color__interactive;
                    }
                    
                }
                
                .byline,
                .posted-on
                .comments-link
                .edit-link{
                    display: block;
                    margin-bottom: 1em;
                    &:hover,
                        &:focus{
                        margin: 0;
                        content: "";
                    }
                    
                    
                }
                
                
                .extra{
                    display: inline;
                }
                
            }
            
            
            
        }
    }
    
    
}