.gallery {
	margin: 1.5em 0;
}

.gallery-item {
	display: inline-block;
	text-align: center;
	vertical-align: top;
	width: 100%;
        padding: 1em;
        
        img{
            display: block;
        }
        
        a{
            display: inline-block;
            border-bottom: none;
            
            &:hover,
                &:focus{
                border-botton:none;
                box-shadow:none;
                
                img{
                    border-color: $color__interactive;
                }
            }
            
            img {
                padding: .5em;
                margin: 0 auto;
                border: 1px solid #fff;
            }
        }

	// Loops to enumerate the classes for gallery columns.
	@for $i from 2 through 9 {
		.gallery-columns-#{$i} & {
			max-width: ( 100% / $i );
		}
	}
}

.gallery-columns-2 .gallery-item{
    max-width:50%;
}

.gallery-columns-3 .gallery-item{
    max-width:33.33%;
}

.gallery-columns-4 .gallery-item{
    max-width:25%;
}

.gallery-columns-5 .gallery-item{
    max-width:20%;
}

.gallery-columns-6 .gallery-item{
    max-width:16.66%;
}

.gallery-caption {
	display: block;
}
